<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <p>{{ message }}</p>
    <input v-model="message">
    <div v-bind:id="dynamicId">
    <p>Using mustaches: {{ rawHtml }}</p>
    </div>
    <p>Using v-html directive:
      <span v-html="rawHtml">This should be red</span>
    </p>
    <button v-bind:disabled="isButtonDisabled">Button</button>
  </div>
</template>

<script>
import Vue from 'vue'
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      message: 'Hello Vue!',
      rawHtml: '<span style="color:red">This should be red</span>'
    }
  }
}
// 我们的数据对象
var data = { a: 1 }
// 该对象被加入到一个 Vue 实例中
var vm = new Vue({
  data: data
})
// 获得这个实例上的属性
// 返回源数据中对应的字段
// eslint-disable-next-line no-unused-expressions,eqeqeq
var b = vm.a == data.a // => true
console.log(b)
// 设置属性也会影响到原始数据
vm.a = 2
console.log(data.a) // => 2

// ……反之亦然
data.a = 3
// eslint-disable-next-line no-unused-expressions
vm.a // => 3
vm.b = 'hi'
console.log('data.a :' + data.a)
console.log('data.b :' + vm.b)
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



